*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: rgb(44, 134, 67);
}
#calculator{
  display: flex;
  flex-direction: column;
  padding: 5px;
  background-color: rgb(15, 15, 15);
}
#display-container{
  width: 402px;
  min-height: 150px;
  height: max-content;
  display: flex;
  align-items: flex-end;
  flex-direction: column-reverse;
  text-align: center;
  font-family: 'Changa', sans-serif;
  font-weight: bold;
  font-size: 40px;
  text-align: right;
  word-break: break-all;
}

#upper-text{
  font-size: 30px;
  color: rgb(255, 255, 123);
  
}
#display{
  font-size: 45px;
  color: white;
}
#buttons{
  display: grid;
  row-gap: 1px;
  column-gap: 1px;
  height: 450px;
  grid-template-areas: 
  "clear clear divide multiply"
  "seven eight nine subtract"
  "four five six add"
  "one two three equals"
  "zero zero decimal equals"; 
}
#clear{
  grid-area: clear;
  background-color: rgb(107, 27, 27) !important;
}
#equals{
  grid-area: equals;
  background-color: rgb(31, 31, 105) !important;
}
#zero{
  grid-area: zero;
}
#buttons button{
  display: flex;
  outline: none;
  justify-content: center;
  align-items: center;
  background-color: #026100;
  color: white;
  cursor: pointer;
  border-radius: 0;
  border: none;
  font-family: 'Changa', sans-serif;
  font-weight: bold;
  font-size: 30px;
}
#buttons button:hover {
  filter: brightness(0.9);
}